.c-project-card {
  display: block;
  border-radius: 2rem;
  overflow: hidden;
  width: 28rem;
  height: 36rem;
  background: var(--page-background-color);
  box-shadow: 0 0 0 rgba(0, 0, 0, .4);
  transition: box-shadow .4s;

  & > * {
    height: 50%;
  }

  &:hover {
    box-shadow: 0 0 2rem rgba(0, 0, 0, .8);

    .slider {
      transform: translateX(-50%);
    }

    .overlay {
      opacity: 1;
      visibility: visible;
    }
  }

  .img-container {
    position: relative;
    width: 100%;
    text-align: center;
    background: rgba(255, 255, 255, .01);
    overflow: hidden;

    img {
      width: 100%;
      min-height: 100%;
      object-fit: cover;
    }

    span {
      font-size: 2rem;
      color: rgba(255, 255, 255, .1);
      font-weight: 700;
      line-height: 20rem;
      text-transform: uppercase;
      text-align: center;
    }
  }

  .content {
    padding: 2rem 0 0;
  }

  h3 {
    margin: 0 0 1.6rem 0;
    font-size: 1.8rem;
    padding: 0 2rem;
  }

  p {
    font-size: 1.5rem;
    line-height: 130%;
    padding: 0 2rem;
    margin: 0;
  }

  .overlay {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    visibility: hidden;
    transition: opacity .2s;
    background: rgba(0, 0, 0, .8);

    a {
      $color: #EEE;

      display: block;
      width: 9rem;
      padding: 1rem;
      border: 1px solid currentColor;
      border-radius: 1rem;
      color: $color;
      text-decoration: none;
      font-weight: 700;
      font-size: 1.4rem;

      &:not(:last-child) {
          margin-bottom: 1rem;
      }
    }
  }

  .content {
    .slider {
      display: flex;
      align-items: center;
      width: 200%;
      transition: transform .2s;

      & > * {
        width: 50%;
      }
    }
  }

  .tag-list {
    padding: 0 2rem;
    margin: 0;
    text-align: center;

    li {
      display: inline-block;
      padding: 0;
      margin: 0;
    }
  }
}